    import {concatImgUrl} from "@/utils/index"
export default {
  props:{
    carouselList: {
      type: Array,
      default: []
    },

  },
    computed:{
        getImgUrl(){
            return function(path){
                return	concatImgUrl(path)

            }
        },
    },
  data(){
    return{
        bannerHeight:"500px"
    }
  },
    mounted() {
        this.imgLoad();
    },
    methods:{
        handleAtomDetail(item){
            window.open (item.linkUrl)
        },
        imgLoad(){
            this.$nextTick(()=>{

                this.bannerHeight = window.screen.height / 3 + 'px'

            })
        }

  },

  watch: {

  }

}
